
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

export default {
components: {
XHeader, Grid, GridItem, GroupTitle
},
data() {
return {
routeName: this.$route.name,
id: this.$route.query.id,
contantShow: true,
showList: {
'class': '', look: '666', who: '',
'img': ['../../../../static/imgs/my.jpg', '../../../../static/imgs/my.jpg', '../../../../static/imgs/my.jpg', '../../../../static/imgs/my.jpg', '../../../../static/imgs/xiaoji.jpg', '../../../../static/imgs/xiaoji.jpg',],
'title': '', 'contant': '', 'time': '2017-07-18 18:53:00'
},
}
},
methods: {

},
created() {
this.routeName = this.showList.title
document.title = this.$route.name

}
}



